 /* ------------------------Chatbot codes ------------------------- */
 .chatbot #click{
  display: none;
}
.chatbot label {
  position: fixed;
  right: 1.2rem;
  bottom: 1rem;
  height: 3.5rem;
  width: 3.5rem;
  background: -webkit-linear-gradient(right, #0fd619, #4470ff);
  text-align: center;
  line-height: 3.5rem;
  border-radius: 3.2rem;
  font-size: 2rem;
  color: #fff;
  cursor: pointer;
}
.chatbot label i{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.4s ease;
}
.chatbot label i.fas{
  opacity: 0;
  pointer-events: none;
}
#click:checked ~ label i.fas{
  opacity: 1;
  pointer-events: auto;
  transform: translate(-50%, -50%) rotate(180deg);
}
#click:checked ~ label i.fab{
  opacity: 0;
  pointer-events: none;
  transform: translate(-50%, -50%) rotate(180deg);
}
.wrapper{
  position: fixed;
  right: 2rem;
  bottom: 0rem;
  max-width: 22rem;
  background: #fff;
  border-radius: 1rem;
  box-shadow: 0px 15px 20px rgba(0,0,0,0.1);
  opacity: 0;
  pointer-events: none;
  transition: all 0.6s cubic-bezier(0.68,-0.55,0.265,1.55);
}
#click:checked ~ .wrapper{
  opacity: 1;
  bottom: 5.5rem;
  pointer-events: auto;
}
.wrapper .head-text{
  line-height: 3.5rem;
  color: #fff;
  border-radius: 1rem 1rem 0 0;
  padding: 0 1.2rem;
  font-weight: 500;
  font-size: 1.2rem;
  background: -webkit-linear-gradient(right, #0aa112, #4470ff);
}
.wrapper .chat-box{
  padding: 1.2rem;
  width: 100%;
}
