.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.blank{
  color: red;
}

.previewPaneHead{
  color: rgb(241, 243, 247);
}
/* Timer Container  */
.TimerContainer{
  font-size: medium;
  font-weight: bold;
  padding: .4rem 1.5rem;
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
}
.TimerContainer span span{
  margin: .2rem;
  padding-bottom: .4rem;
}
.textbox{
  padding: .1rem;
}
/* Buttons */

.newfeature{
  /* background-color: #12a79a; */
  font-size: medium;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.textbox{
  border-top-right-radius: 1rem;
  border-bottom-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
}
.textbox textarea{
  /* border: 1px solid red; */
  border-radius: 1rem;
}
#timerLiveTime{
  text-shadow: 10px 10px 50px rgb(102, 102, 102)                    
}
.openPopup {
  border-radius: .4rem;
  color: white;
  padding: .6rem 1rem;
  margin: .2rem;
  background-color: #a2238e;
  border: none;
  white-space: normal;
  font-size: smaller;
  letter-spacing: 0.2px;
  /* box-shadow: rgba(255, 255, 255, 0.678) 0px 7px 29px 0px; */
}

.openPopup:hover, #openPopup:active, #openPopup:focus {
  box-shadow: rgba(34, 235, 151, 0.678) 0px 7px 29px 0px;
  
}
